html.fp-enabled, .fp-enabled body {
	margin: 0;
	padding: 0;
	overflow: hidden;
	/*Avoid flicker on slides transitions for mobile phones #336 */
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

#superContainer {
	height: 100%;
	position: relative;
	/* Touch detection for Windows 8 */
	-ms-touch-action: none;
	/* IE 11 on Windows Phone 8.1 */
	touch-action: none; }

.fp-section {
	position: relative;
	-webkit-box-sizing: border-box;
	/* Safari<=5 Android<=3 */
	-moz-box-sizing: border-box;
	/* <=28 */
	box-sizing: border-box; }

.fp-slide {
	float: left;
	height: 100%;
	display: block; }

.fp-slidesContainer {
	height: 100%;
	display: block; }

.fp-slides {
	z-index: 1;
	height: 100%;
	overflow: hidden;
	position: relative;
	-webkit-transition: all 0.3s ease-out;
	/* Safari<=6 Android<=4.3 */
	transition: all 0.3s ease-out; }

.fp-section.fp-table, .fp-slide.fp-table {
	display: table;
	table-layout: fixed;
	width: 100%; }

.fp-tableCell {
	display: table-cell;
	vertical-align: middle;
	width: 100%;
	height: 100%; }

.fp-slidesContainer {
	float: left;
	position: relative; }

.fp-controlArrow {
	-webkit-user-select: none;
	/* webkit (safari, chrome) browsers */
	-moz-user-select: none;
	/* mozilla browsers */
	-khtml-user-select: none;
	/* webkit (konqueror) browsers */
	-ms-user-select: none;
	/* IE10+ */
	position: absolute;
	z-index: 4;
	top: 50%;
	cursor: pointer;
	width: 0;
	height: 0;
	border-style: solid;
	margin-top: -38px;
	-webkit-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	&.fp-prev {
		left: 15px;
		width: 0;
		border-width: 38.5px 34px 38.5px 0;
		border-color: transparent #fff transparent transparent; }
	&.fp-next {
		right: 15px;
		border-width: 38.5px 0 38.5px 34px;
		border-color: transparent transparent transparent #fff; } }

.fp-scrollable {
	overflow: hidden;
	position: relative; }

.fp-scroller {
	overflow: hidden; }

.iScrollIndicator {
	border: 0 !important; }

.fp-notransition {
	-webkit-transition: none !important;
	transition: none !important; }

#fp-nav {
	position: fixed;
	z-index: 100;
	margin-top: -32px;
	top: 50%;
	opacity: 1;
	-webkit-transform: translate3d(0, 0, 0);
	&.right {
		right: 17px; }
	&.left {
		left: 17px; } }

.fp-slidesNav {
	position: absolute;
	z-index: 4;
	left: 50%;
	opacity: 1;
	-webkit-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	&.bottom {
		bottom: 17px; }
	&.top {
		top: 17px; } }

#fp-nav ul {
	position: relative;
	&::after {
		position: absolute;
		top: -50px;
		left: -120px;
		content: '';
		width: 140px;
		height: 300px;
		transform: translateX(40px);
		background-repeat: no-repeat;
		background-size: contain;
		background-image: url(../img/menu.svg);
		background-position: left;
		transition: all 0.2s ease;
		z-index: -1;
		border-radius: 50% 0 0 50%; }
	&:hover {
		&::after {
			transform: scale(1.5) translateX(10px); } } }

#fp-nav ul, .fp-slidesNav ul {
	margin: 0;
	padding: 0; }

#fp-nav ul li {
	display: block;
	width: 14px;
	height: 13px;
	margin: 14px;
	position: relative;
	opacity: 0;
	transition: all 0.3s ease;
	transform: translateX(100%); }

.fp-slidesNav ul li {
	display: block;
	width: 14px;
	height: 13px;
	margin: 7px;
	position: relative;
	display: inline-block; }

#fp-nav ul li a, .fp-slidesNav ul li a {
	display: block;
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100%;
	cursor: pointer;
	text-decoration: none; }

#fp-nav ul li a.active span, .fp-slidesNav ul li a.active span, #fp-nav ul li:hover a.active span, .fp-slidesNav ul li:hover a.active span {
	height: 14px;
	width: 14px;
	margin: -6px 0 0 -6px;
	border-radius: 100%;
	background: #fff; }

#fp-nav ul li a span, .fp-slidesNav ul li a span {
	border-radius: 50%;
	position: absolute;
	z-index: 1;
	height: 8px;
	width: 8px;
	background: transparent;
	border: 1px solid #fff;
	left: 50%;
	top: 50%;
	margin: -2px 0 0 -2px;
	transition: all 0.2s ease-in-out; }

#fp-nav ul li:hover a span, .fp-slidesNav ul li:hover a span {
	width: 14px;
	height: 14px;
	background: #fff;
	margin: -5px 0px 0px -5px; }

#fp-nav {
	ul li {
		.fp-tooltip {
			position: absolute;
			top: -1px;
			color: #fff;
			font-size: 12px;
			font-family: $GothamProLight;
			white-space: nowrap;
			padding-top: 1px;
			max-width: 230px;
			overflow: hidden;
			display: block;
			opacity: 0.7;
			cursor: pointer;
			transition: all 0.2s ease-in; }
		&:hover .fp-tooltip {
			transition: all 0.2s ease-in;
			width: auto;
			opacity: 1;
			transform: scale(1.1) translate(-7px, 0); } }
	&.fp-show-active a.active + .fp-tooltip {
		transition: all 0.2s ease-in;
		width: auto;
		opacity: 1;
		transform: scale(1.1) translate(-7px, 0); }
	ul li .fp-tooltip {
		&.right {
			right: 20px; }
		&.left {
			left: 20px; } } }

.fp-auto-height {
	&.fp-section, .fp-slide, .fp-tableCell {
		height: auto !important; } }

.fp-responsive .fp-auto-height-responsive {
	&.fp-section, .fp-slide, .fp-tableCell {
		height: auto !important; } }
