.mod__closer--lines::before, .mod__closer--lines::after { transition: all 0.15s linear; position: absolute; content: ''; width: 34px; height: 1px; background-color: #fff; box-shadow: 0 0 1px rgba(255, 255, 255, 0.5); }

.mod__closer--lines::after { left: 0; top: 0; transform: rotate(45deg); }

.mod__closer--lines::before { left: 0; top: 0; transform: rotate(135deg); }

p.require { display: none; position: absolute; bottom: 0; right: 0; background-color: #fff; color: #000; width: 50%; font-size: 16px; font-family: "GothamProMedium", sans-serif; text-align: center; padding: 5px; }

